<template>
  <div class="newsdetail">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span class="card-title">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/news' }">{{ $t('commom.newslist') }}</el-breadcrumb-item>
            <el-breadcrumb-item>{{ $t('commom.newsdetail') }}</el-breadcrumb-item>
          </el-breadcrumb>
        </span>
      </div>
      <div v-html="newsHtml"></div>
    </el-card>
  </div>
</template>

<script>
import contentZh from '../static/news-zh'
import contentEn from '../static/news-en'
export default {
  name: 'NewsDetail',
  data () {
    return {
      rawHtml: '',
      index: null
    }
  },
  computed: {
    newsHtml () {
      const index = this.$store.state.news.index
      if (index !== null) {
        if (this.$i18n.locale === 'en') {
          return contentEn[index].content
        }
        return contentZh[index].content
      } else {
        if (this.$i18n.locale === 'en') {
          return '<h1>Loading...</h1>'
        }
        return '<h1>数据正在加载...</h1>'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/variables.scss";
.newsdetail {
  .card-title {
    height: 40px;
  }
}
.write {
  text-align: center;
}
.indent {
  text-indent: 2.5em;
}
</style>
<style scoped>
.box-card >>> .el-card__header {
  padding: 15px 20px;
  background: linear-gradient(to bottom, #efdbca, #fdd650);
  font-size: 16px;
  border: 0px solid #f9d76c;
}
</style>



